import groups from '../data/groups';

function groupBy(arr, key) {
  return arr.reduce((acc, val) => {
    return {
      ...acc,
      [val[key]]: [...(acc[val[key]] || []), val]
    };
  }, {});
}

export default () => {
  const groupedGroups = groupBy(groups, 'status');
  return (
    <ul>
      {Object.keys(groupedGroups).map(status => (
        <h2>{status}</h2>
      ))}
    </ul>
  );
};
